@tailwind base;
html {
  scroll-behavior: smooth;
}
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu {
  transition: opacity 1s, height 1s, padding-top 1s;
}

.open {
  height: 35vh;
  opacity: 1;
  padding-top: 5rem;
}

.close {
  height: 0vh;
  opacity: 0;
  padding-top: 0rem;
}

.blur_purple1 {
  background: #831ED2;
  opacity: 0.85;
  filter: blur(120px);

}
.blur_purple2 {
  background: #831ED2;
  opacity: 0.4;
  filter: blur(60px);

}

@media (max-width: 768px) {


  .blur_purple2 {
    background: #831ED2;
    opacity: 0.9;
    filter: blur(50px);
  
  }

  .blur_purple1 {
    background: #831ED2;
    opacity: 1;
    filter: blur(80px);
  
  }
}



li {
  margin-bottom: 0.15rem;
}

.gradient1 {
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,1) 27%, rgba(66,66,66,0.7637429971988796) 70%, rgba(255,255,255,0) 100%);
}